import React from 'react';
import './statbox.css';

const StatsBox = ({ dollarIncome, hoursPassed, daysPassed, yearsPassed, mainHappiness, happinessIncome, spending, annualBonus, numberWithCommas}) => {
    let displayWage = numberWithCommas(dollarIncome);
    let displayAnnualBonus = numberWithCommas(annualBonus);
    let displayHappy = numberWithCommas(mainHappiness);
    let displayHappyIncome = numberWithCommas(happinessIncome);
    let displayCostofLiving = numberWithCommas(spending);
    return (
        <div id='statbox' className='statbox light-gray'>
            <p className='statincome light-green'>You earn ${displayWage}/hour, 24 hours a day.<br></br> &#160;&#160;&#160;&#160;Annual Bonus = ${displayAnnualBonus}</p>
            <p className='costliving'>Cost of living: ${displayCostofLiving} / day</p>
            <p className='stathappy'>Happy &#x1F604;: {displayHappy}. &#160;&#160;&#160;&#160;&#160; You earn &#x1F604;: {displayHappyIncome} per day.</p> 
            <p className='stathappy'></p> 
            

            <p className='light-yellow'>{Math.round(yearsPassed)} years / {Math.round(daysPassed)} days / {hoursPassed} hours passed</p>


        </div>
    );
}

export default StatsBox;